<template>
  <div :style="outSize">
    <div class="blowUpShow" v-show="isBlowUp">
      <i class="el-icon-circle-close" @click="isBlowUp=false"></i>
      <img id="blowUpImg" :src="imgPath">
    </div>
    <!-- <img src="../../static/img/whitebackground.jpg" preview> -->
    <div class="uploadBox" v-for="(value,index) in imgInfo" :key="index">
      <input
        type="file"
        class="inputStyle"
        :ref="'upload'+index"
        @change="uploadFile(index,value)"
        :style="outBorder"
      >
      <div class="showImg" :style="outBorder">
        <img
          :ref="'img'+index"
          :src="value.src"
          :width="value.width"
          :height="value.height"
          class="imgPrew"
        >
      </div>
      <div class="operateImg" :style="outBorder">
        <i
          v-show="value.src=='../../static/img/whitebackground.jpg'"
          class="el-icon-circle-plus-outline"
          @click="imgAdd(index)"
        ></i>
        <i
          v-show="value.src!='../../static/img/whitebackground.jpg'"
          class="el-icon-zoom-in"
          @click="imgBlowUp(index)"
        ></i>
        <i
          v-show="value.src!='../../static/img/whitebackground.jpg'"
          class="el-icon-delete"
          @click="imgDelete(index)"
        ></i>
      </div>
    </div>
    <div class="btnUpload">
      <el-button @click="imgUpload()">上传图片</el-button>
    </div>
  </div>
</template>
<script>
import tools from "../tools/tools";
import axios from "axios";
export default {
  name: "UpLoad",
  data() {
    return {
      isBlowUp: false,
      outSize: {
        weidth: "100%",
        height: "400px",
        overflow: "hidden"
      },
      imgPath: "",
      outBorder: {
        width: "120px",
        height: "120px",
        overflow: "hidden",
        borderRadius: "5px"
      },
      imgInfo: [
        {
          src: "../../static/img/whitebackground.jpg",
          width: "100%",
          height: "100%"
        }
      ],
      //图片最大数量
      maxSize: 12,
      uploadData: new FormData()
    };
  },
  created() {},
  mounted() {},
  // computed:{
  //   uploadData:function(){
  //     var data=new FormData();
  //     return data;
  //   }
  // },
  methods: {
    uploadFile: function(n) {
      //判断是否支持FileReader
      var _this = this;
      if (window.FileReader) {
        var reader = new FileReader();
      } else {
        alert("您的设备不支持图片预览功能，如需该功能请升级您的设备！");
      }
      var file = this.$refs["upload" + n][0].files[0];
      var imageType = /^image\//;
      //是否是图片
      if (file) {
        if (!imageType.test(file.type)) {
          alert("请选择图片！");
          return;
        }
      }
      //读取完成
      reader.onload = function(e) {
        //获取图片dom
        //图片路径设置为读取的图片
        if (_this.imgInfo.length < _this.maxSize + 1) {
          if (_this.imgInfo.includes(e.target.result)) {
            tools.warnNotice("图片已选取");
          } else {
            _this.imgInfo.unshift({
              src: e.target.result,
              width: "",
              height: ""
            });
            if (_this.imgInfo.length == _this.maxSize + 1) {
              _this.imgInfo.splice(-1, 1);
              tools.warnNotice("最多上传" + _this.maxSize + "张图片");
            }
          }
        } else {
          return false;
        }
      };
      //result 读取为data开头的url字符串
      console.log(file);
      reader.readAsDataURL(file);
      reader.onloadend = function(e) {
        //  设置图片大小
        let img = document.getElementsByClassName("imgPrew");
        // console.log(img);
        if (img[0]) {
          _this.imgInfo[0].imgWidth = img[0].width;
          _this.imgInfo[0].imgHeight = img[0].height;
          if (img[0].width < img[0].height) {
            _this.imgInfo[0].width = "auto";
            _this.imgInfo[0].height = "100%";
          } else {
            _this.imgInfo[0].width = "100%";
            _this.imgInfo[0].height = "auto";
          }
        }
      };
    },
    imgAdd: function(n) {
      document.getElementsByClassName("inputStyle")[0].click();
    },
    imgDelete: function(n) {
      this.imgInfo.splice(n, 1);
      console.log(
        this.imgInfo[this.imgInfo.length - 1].src ==
          "../../static/img/whitebackground.jpg"
      );
      if (
        this.imgInfo[this.imgInfo.length - 1].src !==
        "../../static/img/whitebackground.jpg"
      ) {
        this.imgInfo.push({
          src: "../../static/img/whitebackground.jpg",
          width: "100%",
          height: "100%"
        });
      }
    },
    imgBlowUp: function(n) {
      this.isBlowUp = true;
      this.imgPath = this.imgInfo[n].src;
    },
    imgUpload: function() {
      var _this = this;
      // this.uploadData = new FormData();
      this.imgInfo.forEach(function(value, index, arr) {
        if (index < arr.length - 1) {
          _this.uploadData.append("image" + index, value);
        }
        axios
          .post("127.0.0.1",_this.uploadData)
          .then(function(response) {
            console.log(response);
          })
          .catch(function(error) {
            console.log(error);
          });
      });
    }
  },
  components: {}
};
</script>
<style  scoped>
.uploadBox {
  display: inline-block;
  width: 120px;
  height: 120px;
  margin: 10px 0 0 10px;
  overflow: hidden;
}
.inputStyle {
  display: inline-block;
  background-color: red;
  /* width: 120px;
  height: 120px; */
  opacity: 0;
  position: relative;
  vertical-align: top;
  z-index: 200;
}
.showImg {
  display: inline-flex;
  background-color: rgb(228, 228, 240);
  /* width: 120px;
  height: 120px; */
  vertical-align: top;
  font-size: 40px;
  position: relative;
  top: -120px;
  justify-content: center;
  align-items: center;
}
.showImg > img {
  display: inline-block;
  flex: 1;
}
.showImg > i {
  display: inline-block;
}
.operateImg {
  /* width: 120px;
  height: 120px; */
  line-height: 120px;
  text-align: center;
  position: relative;
  top: -240px;
  font-size: 30px;
  color: rgba(186, 190, 192, 0.425);
  z-index: 300;
}
.blowUpShow {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(233, 233, 240);
  position: absolute;
  left: 0;
  top: 0;
  /* right: 0;
  bottom: 0; */
  z-index: 400;
}
.blowUpShow > img {
  max-width: 100vw;
}
.blowUpShow > i {
  width: 40px;
  font-size: 30px;
  position: absolute;
  top: 10px;
  right: 25px;
  z-index: 400;
  color: rgb(192, 185, 185);
}
.btnUpload {
  width: 100%;
  height: 80px;
  line-height: 80px;
}
</style>


