<template>
  <div>
    <myHeader></myHeader>
    <div style="display:flex">
      <myAsider></myAsider>
      <div class="contentArea">
        <myBDMap></myBDMap>
      </div>
    </div>
  </div>


</template>

<script>
  import Header from '../components/Header'
  import Asider from '../components/Asider'
  import BDMap from '../components/BDMap'
  export default {
    name: "baiDuMap",
    data() {
      return {}
    },
    components: {
      myHeader: Header,
      myAsider: Asider,
      myBDMap:BDMap,
    }
  }
</script>

<style scoped>
 .contentArea {
      flex: 1;
      margin: 5px 0 0 0;
      display: block;
      width: 100%;
      background-color: lightgray;
      height: calc(100vh - 105px);
      overflow-x: hidden;
      overflow-y: scroll;
  }
</style>



