<template>
    <div>
        <myHeader></myHeader>
        <div style="display:flex">
            <myAsider></myAsider>
            <div class="contentArea" style="flex:1">
                <div class="showGrid">
                    <div class="item1">1</div>
                    <div class="item2">2</div>
                    <div class="item3">3</div>
                    <div class="item4">4</div>
                    <div class="item5">5</div>
                    <div class="item6">6</div>
                    <div class="item7">7</div>
                    <div class="item8">8</div>
                    <div class="item9">9</div>
                    <div class="item0">0</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Header from '../components/Header'
    import Asider from '../components/Asider'

    export default {
        name: "flexGrid",
        data() {
            return {}
        },
        components: {
            myHeader: Header,
            myAsider: Asider,
        }
    }
</script>
<style scoped>
     .contentArea {
      flex: 1;
      margin: 5px 0 0 0;
      display: block;
      width: 100%;
      background-color: lightgray;
      height: calc(100vh - 105px);
      overflow-x: hidden;
      overflow-y: scroll;
  }
    .item1 {
        grid-area: item1;
        background-color: #ffdafe;
    }

    .item2 {
        grid-area: item2;
        background-color: #56ff63;
    }

    .item3 {
        grid-area: item3;
        background-color: #8dffff;
    }

    .item4 {
        grid-area: item4;
        background-color: #1537ff;
    }

    .item5 {
        grid-area: item5;
        background-color: #b15dff;
    }

    .item6 {
        grid-area: item6;
        background-color: #ffc046;
    }

    .item7 {
        grid-area: item7;
        background-color: #ffc822;
        width:50%;
        height: 50%;
    }

    .item8 {
        grid-area: item8;
        background-color: #ff2794;
    }

    .item9 {
        grid-area: item9;
        background-color: #49ff5a;
    }

    .item0 {
        grid-area: item0;
        background-color: #d1b1ff;
    }

    .showGrid {
        width: 100%;
        height: 100%;
        display: grid;
        /*划分列*/
        grid-template-columns: 1fr 3fr 3fr 1fr;
        /*划分行*/
        grid-template-rows: 1fr 1fr 40px 1fr 1fr 1fr;
        /*行列间距*/
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        /*内容位置 居中是按网格数计算居中不是按长度*/
        justify-items: center;
        align-items: center;
        grid-template-areas: ". item1 item1 ." ". item2 item3 item7" "item4 item5 item6 item7" "item8 item9 item0 item7" ". item9 item0 ."
    }

</style>



